<template>
  <div class="upload">
    <div :style="{display: inline}">
      <a @click="showUpload('cert')">Certification</a> / 
      <a @click="showUpload('proj')">Project</a> / 
      <a @click="showUpload('exp')">Experience</a>
    </div>
    <div v-if="isCertify">
      <CertificationUpload></CertificationUpload>
    </div>
    <div v-if="isProj">
      <ProjectUpload></ProjectUpload>
    </div>

  </div>
</template>

<script>
import CertificationUpload from "../components/uploadCertifications.vue";
import ProjectUpload from "../components/uploadProjects.vue";
export default {
  components: {
    CertificationUpload,
    ProjectUpload,
  },
  data: function () {
    return {
      isCertify: false,
      isExp: false,
      isProj: false,
    };
  },
  methods: {
    showUpload: function (type) {
      if (type == "cert") {
        this.isCertify = true;
        this.isExp = false;
        this.isProj = false;
      } else if (type == "exp") {
        this.isCertify = false;
        this.isExp = true;
        this.isProj = false;
      } else if (type == "proj") {
        this.isCertify = false;
        this.isExp = false;
        this.isProj = true;
      }
    },
  },
};
</script>
<style scoped>
div div a{
  cursor: pointer;
}
</style>