<template>
  <div class="upload">
    <h3>Upload Projects</h3>
    <center class="container">
      <div class="upload">
        <div class="section-1">
          <div>
            <h5>
              Project Title
              <input
                @change="titleAdd"
                class="title"
                type="text"
                maxlength="40"
                minlength="4"
                name="title"
                placeholder="Type here...."
              />

              <div>
                Month & Year
                <input
                  @change="yearAdd"
                  class="year"
                  type="text"
                  name="year"
                  maxlength="10"
                  minlength="4"
                  placeholder="Type here...."
                />
              </div>
              <div>
                Programming Languages
                <input
                  @change="languageAdd"
                  class="languages"
                  type="text"
                  name="languages"
                  maxlength="40"
                  minlength="4"
                  placeholder="Type here...."
                />
              </div>
              <div>
                Project Description(100 letters)
                <textarea
                  @change="descriptionAdd"
                  class="description"
                  name="description"
                  maxlength="100"
                  minlength="4"
                  placeholder="Type here...."
                />
              </div>
              <div>
                GitHub Link
                <input
                  @change="gitLinkAdd"
                  class="gitLink"
                  type="text"
                  name="gitLink"
                  maxlength="100"
                  minlength="0"
                  placeholder="Type here...."
                />
              </div>
              <div>
                Site Link
                <input
                  @change="siteLinkAdd"
                  class="siteLink"
                  type="text"
                  name="siteLink"
                  maxlength="100"
                  minlength="0"
                  placeholder="Type here...."
                />
              </div>
            </h5>
            <input
              class="img-pick"
              type="file"
              @change="previewImage"
              accept="*"
            />
            <div>
              <p>
                Progress: {{ uploadValue.toFixed() + "%" }}
                <progress
                  id="progress"
                  :value="uploadValue"
                  max="100"
                ></progress>
              </p>
            </div>
          </div>
          <div v-if="imageData != null">
            <br />
            <button @click="onUpload" class="upload-btn">
              <i class="fas fa-cloud-upload-alt"></i>
            </button>
          </div>
        </div>
        <img class="preview" :src="picture" />
      </div>
    </center>
  </div>
</template>

<script>
import firebase from "firebase/app";
import "firebase/firebase-storage";
import { db } from "../main";

export default {
  name: "ProjectUpload",
  data() {
    return {
      title: null,
      year: null,
      languages: null,
      description: null,
      gitLink: null,
      siteLink: null,
      imageData: null,
      picture: null,
      uploadValue: 0
    };
  },
  methods: {
    titleAdd(e) {
      this.title = e.target.value;
    },
    yearAdd(e) {
      this.year = e.target.value;
    },
    languageAdd(e) {
      this.languages = e.target.value;
    },
    descriptionAdd(e) {
      this.description = e.target.value;
    },
    gitLinkAdd(e) {
      this.gitLink = e.target.value;
    },
    siteLinkAdd(e) {
      this.siteLink = e.target.value;
    },
    previewImage(event) {
      this.uploadValue = 0;
      this.picture = null;
      this.imageData = event.target.files[0];
    },

    onUpload() {
      this.picture = null;
      const storageRef = firebase
        .storage()
        .ref()
        .child(`/projects_screenshot`)
        .child(`${this.imageData.name}`)
        .put(this.imageData);
      storageRef.on(
        `state_changed`,
        snapshot => {
          this.uploadValue =
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        },
        error => {
          console.log(error.message);
        },
        () => {
          this.uploadValue = 100;

          storageRef.snapshot.ref.getDownloadURL().then(url => {
            db.collection("projects")
              .doc()
              .set({
                project_title: this.title,
                project_date: this.year,
                languages: this.languages,
                project_description: this.description,
                gitHub_link: this.gitLink,
                site_url: this.siteLink,
                image_url: url
              });
            this.picture = url;
          });
        }
      );
    }
  }
};
</script>
<style scoped>
img.preview {
  width: 60vw;
}
.section-1 {
  width: 60vw;
  padding: 15px;
  border-radius: 10px;
  background: rgb(165, 80, 0);
  margin: 10px;
  overflow: hidden;
}
.container {
  display: flex;
  flex-direction: column;
}
.upload {
  margin-right: 20vw;
}
.img-pick {
  color: rgb(255, 255, 255);
}
.upload {
  padding: 5px;
}
.upload-btn {
  border-radius: 10px;
  background-color: rgb(255, 131, 15);
  width: 120px;
}

.upload-btn i {
  font-size: 20px;
  padding: 5px;
}
@media (max-width: 800px) {
  .container {
    flex-direction: column;
  }
}
</style>
